.slider-product {
  display: flex;
  justify-content: space-between;
  overflow-y: none;
}

.slider-product__thumbs.swiper-container {
  position: relative;
  display: flex;
  height: 380px;
  align-item: center;
  margin: 0 auto;
  overflow-y: hidden;



  outline: 1px solid blue;
}

.slider-product__wrap.swiper-wrapper {
  display: flex;
  width: 178px;
  margin-top: 62px;
}

.slider-product__thumb.swiper-slide {
  display: flex;
  align-items: center;
  width: 178px;
  border: 2px solid transparent;
  cursor: pointer;
  opacity: 0.5;

  outline: 1px solid red;
}

.slider-product__thumb.swiper-slide-thumb-active {
  opacity: 1;
  border: 2px solid var(--basic-link);
}


.slider-product__thumb figure {
  width: 140px;
  height: 136px;
  margin: 0 auto;
}

.slider-product__toggle {
  position: absolute;
  display: block;
  width: 50px;
  height: 30px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-repeat: no-repeat;
  pointer-events: auto;
  cursor: pointer;
  z-index: 10;
}

.slider-product__toggle--next {
  background-image: url("../img/gallery-arrow.png");
}

.slider-product__toggle--prev {
  top: 523px;
  background-image: url("../img/gallery-arrow.png");
  transform: scaleY(-1) translateX(-50%);
}

.slider-product.swiper-container-autoheight figure {
  width: 100%; /* Ширина области */
  height: 178px; /* Высота области */
  margin: 0; /* Обнуляем отступы */
  overflow: hidden; /* Прячем всё за пределами */

  @include _960 {

  }
}

