.catalog-nav li {
  margin-bottom: 12px;
}

.catalog-nav a {
  display: block;
  font-size: 32px;
  line-height: 28px;
  font-weight: 300;
  font-variant: small-caps;
  letter-spacing: -1px;
  color: var(--basic-link);
  text-decoration: none;
  padding-top: 8px;
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 10px;
}

.catalog-nav__item-active {
  background-color: var(--button);
}

.catalog-nav__item-active a {
  color: var(--basic-white);
}
