.good-target {
  padding-top: 45px;
  padding-left: 37px;
  padding-bottom: 85px;
}

.good-target--washing {
  padding-bottom: 35px;
}

.good-target__wrap {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  column-gap: 70px;
  margin-bottom: 65px;
}

.good-target__desc {
  width: 1160px;
  height: 275px;
  overflow: hidden;
  margin-right: 0;
  margin-bottom: 33px;
}

.good-target--washing .good-target__wrap {
  margin-bottom: 0;
}

.good-target--washing .good-target__desc {
  height: 100%;
}

.good-target h3 {
  font-family: inherit;
  font-size: 45px;
  line-height: 26px;
  font-weight: 300;
  font-variant: small-caps;
  color: var(--main-text);
  margin: 0;
  margin-bottom: 60px;
}

.good-target p,
.good-target a,
.good-target li {
  font-family: inherit;
  font-size: 23px;
  line-height: 26px;
  font-weight: 100;
  color: var(--main-text);
  margin: 0;
  margin-bottom: 32px;
}

.good-target li {
  position: relative;
  padding-left: 52px;
  margin-bottom: 0;
}

.good-target--washing li::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  top: 50%;
  left: 23px;
  transform: translateY(-50%);
  background-color: var(--basic-link);
}

.good-target a {
  color: var(--basic-link);
}

.good-target button {
  font-family: inherit;
  font-size: 23px;
  line-height: 26px;
  font-weight: 100;
  color: var(--basic-link);
  text-decoration: underline;
  border: none;
  background: none;
  cursor: pointer;
}


