.clean-rooms {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 45px;
}

.clean-rooms__slider {
  width: 100%;
  min-height: 430px;
  display: grid;
  grid-template-columns: repeat(6, 340px);
  column-gap: 74px;
  margin-top: 50px;
  overflow: hidden;
}

.clean-rooms__slider a {
  text-decoration: none;
  cursor: pointer;
}

.clean-rooms__slider img {
  margin-bottom: 30px;
}

.clean-rooms h4 {
  font-size: 38px;
  line-height: 26px;
  font-weight: 300;
  letter-spacing: -1px;
  margin: 0;
  margin-bottom: 25px;
}

.clean-rooms p {
  margin: 0;
}
