.business {
  background-color: var(--back-basic);
}

.business__list {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  column-gap: 90px;
  padding-top: 45px;
  padding-bottom: 57px;
}

.business__list p,
.business__list a {
  color: var(--basic-white);
  margin: 0;
}

.business__list p {
  margin-bottom: 38px;
}

.business__item:nth-child(2) {
  padding-left: 215px;
}

.business__item:nth-child(3) {
  margin-left: 15px;
}

.business__title,
.business__tel {
  font-size: 36px;
  font-weight: 300;
  font-variant: small-caps;
}

p.business__title {
  margin-bottom: 47px;
}

.business__tel {
  display: block;
  line-height: 42px;
  text-decoration: none;
}

.business__info a {
  font-weight: 100;
}
