.good-tabs {
  max-width: 100%;
}

.good-tabs__list {
  display: flex;
}

.good-tabs__list a {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 38px;
  font-weight: 300;
  font-variant: small-caps;
  letter-spacing: -1px;
  color: var(--basic-link);
  margin-right: 45px;
  margin-bottom: 0;
}

a.good-tabs__link-active {
  color: var(--main-text);
  text-decoration: none;
}

.good-tabs__item {
  position: relative;
  display: inline-block;
}

.good-tabs__item input[type="radio"] {
  display: none;
}

.good-tabs__block {
  position: relative;
  display: block;
  padding-top: 60px;
  padding-left: 60px;
  padding-right: 20px;
  padding-bottom: 20px;
}

.good-tabs__block:target {
  display: block;
}

.good-tabs__content ul {
  width: 893px;
}

.good-tabs__content li {
  display: flex;
  align-items: baseline;
  padding-left: 40px;
  border-bottom: 2px solid #c2c3c3;
}

.good-tabs__content li:last-child {
  border-bottom: none;
}

.good-tabs__content p,
.good-tabs__content span {
  font-size: 38px;
  line-height: 43px;
  font-weight: 300;
  font-variant: small-caps;
  letter-spacing: -1px;
  color: var(--main-text);
  margin: 0;
}

.good-tabs__content p {
  width: 732px;
}

.good-tabs__content span {
  font-size: 34px;
}

#tab_02 {
  display: none;
}

#tab_03 {
  display: none;
}

#tab_04 {
  display: none;
}
