.content-inner {
  width: 100%;
}

.content-inner h1 {
  font-family: inherit;
  font-size: 80px;
  line-height: 73px;
  font-weight: 700;
  font-variant: small-caps;
  color: var(--main-text);
  margin: 0;
  margin-bottom: 18px;
}

.content-inner p {
  font-family: inherit;
  font-size: 24px;
  line-height: 28px;
  font-weight: 100;
  color: var(--main-text);
  letter-spacing: -0.6px;
  margin: 0;
}

.content-inner a {
  font-family: inherit;
  font-size: 23px;
  line-height: 26px;
  font-weight: 100;
  color: var(--basic-link);
}

.content-inner__gallery {
  margin-top: 53px;
}
