.product-info {

}

.product-info__slider {

}

.product-info__desc {
  margin-top: 32px;
}

.product-info__desc span {
  display: block;
  font-size: 16px;
  margin-top: 28px;
}

.product-info__tabl {
  width: 727px;
  margin-top: 72px;
}

.product-info__tabl li {
  display: flex;
  align-items: baseline;
  padding-left: 40px;
  border-bottom: 2px solid #c2c3c3;
}

.product-info__tabl li:last-child {
  border-bottom: none;
}

.product-info__tabl p,
.product-info__tabl span {
  font-size: 38px;
  line-height: 43px;
  font-weight: 300;
  font-variant: small-caps;
  letter-spacing: -1px;
  color: var(--main-text);
  margin: 0;
}

.product-info__tabl p {
  width: 340px;
}
