.main-nav {
  display: block;
}

.main-nav__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding-left: 254px;
}

.main-nav li {
  margin-right: 46px;
}

.main-nav li:last-child {
  margin-right: 0;
}

.main-nav__list a {
  color: var(--main-text);
  text-decoration: none;
}






/*  Сворачивание Меню  */


.main-nav__toggle {
  position: absolute;
  display: block;
  top: 30px;
  left: 165px;
  width: 40px;
  height: 16px;
  background-color: transparent;
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 3;
}

.main-nav--closed .main-nav__toggle::before {
  display: block;
  content: "";
  position: absolute;
  top: 0px;
  left: calc(50% - 20px);
  width: 40px;
  height: 2px;
  background-color: var(--back-basic);
  box-shadow: 0 8px 0 0 var(--back-basic), 0 16px 0 0 var(--back-basic);
}

.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  opacity: 0.5;
}

.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.2;
}

.main-nav--opened .main-nav__toggle {
  top: 30px;
  background-color: transparent;
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  width: 40px;
  height: 2px;
  background-color: var(--back-basic);
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  opacity: 0.5;
}

.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.2;
}

.main-nav--closed .main-nav__list li {
  display: none;
}

.main-nav--opened .main-nav__list {
  min-height: 134px;
  padding-top: 35px;
  padding-bottom: 82px;
}
