.form-search {
  position: relative;
  display: flex;
  width: 440px;
  height: 50px;
  align-items: center;
  margin-left: auto;
}

.form-search input[type="search"] {
  width: 405px;
  height: 100%;
  font-size: 23px;
  line-height: 26px;
  color: var(--back-basic);
  padding-left: 25px;
  padding-right: 25px;
  border: none;
  outline: none;
  cursor: pointer;
}

.form-search input[type="search"]::-webkit-input-placeholder {
  visibility: hidden;
  font-family: inherit;
  font-size: 23px;
  line-height: 26px;
  color: var(--back-basic);
}

.form-search:focus input[type="search"]::-webkit-input-placeholder,
.form-search:hover input[type="search"]::-webkit-input-placeholder {
  visibility: visible;
}

.form-search button {
  width: 35px;
  height: 35px;
  background: transparent, url("../img/search.png");
  background-repeat: no-repeat;
  background-position: 0 0;
  margin-left: auto;
  border: none;
  cursor: pointer;
}
