.goods__about h2,
.goods__about p {
  margin: 0;
  margin-left: 40px;
}

.goods__about p {
  font-size: 34px;
  margin-top: 36px;
  margin-bottom: 30px;
}

.goods__picture {
  position: relative;
  width: 1690px;
  transform: translateX(-20px);
}

.goods__img-plus {
  display: block;
  position: absolute;
  width: 58px;
  height: 58px;
  top: 0;
  z-index: 15;
}

.goods__img-plus:nth-child(2) {
  top: 10px;
  left: 397px;
}

.goods__img-plus--active {
  position: absolute;
  width: 359px;
  min-height: 182px;
  top: 445px;
  right: 295px;
}

.goods__img-plus--active img {

}

.goods__img-plus--active p {
  position: absolute;
  width: 275px;
  height: 135px;
  top: 15px;
  left: 0;
  font-family: inherit;
  font-size: 23px;
  line-height: 26px;
  font-weight: 100;
  color: var(--main-text);
  z-index: 10;
}

.goods__img-plus--active a {
  color: var(--main-text);
}

.goods__content {
  width: 100%;
  display: grid;
  grid-template-columns: 798px 1fr;
  column-gap: 47px;
  margin-top: 60px;
}

.goods__content p {
  font-size: 23px;
  line-height: 26px;
  margin: 0;
}
