.catalog-gallery {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 65px;
}

.catalog-gallery li {
  position: relative;
  min-height: 747px;
}

.catalog-gallery a {
  text-decoration: none;
}

.catalog-gallery p {
  font-size: 28px;
  line-height: 30px;
  font-weight: 300;
  letter-spacing: -1px;
  text-transform: uppercase;
}

.catalog-gallery span {
  position: absolute;
  display: block;
  width: 278px;
  bottom: 0;
}

.catalog-gallery img {
  margin-bottom: 63px;
}

.catalog-gallery__btn {
  padding-bottom: 23px;
}

.catalog-gallery__new::after {
  position: absolute;
  content: "";
  width: 155px;
  height: 37px;
  top: 146px;
  right: -50px;
  background-image: url("../img/label-new.png");
  background-repeat: no-repeat;
}
