.form-user {
  padding-top: 50px;
  padding-bottom: 48px;
}

.form-user h2 {
  margin: 0;
  margin-bottom: 42px;
}

.form-user p {
  margin: 0;
}

.form-user a {
  font-size: 23px;
  letter-spacing: -.6px;
}

.form-user__personal {
  display: grid;
  grid-template-columns: repeat(3, 324px);
  column-gap: 125px;
  margin-top: 25px;
  margin-bottom: 40px;
}

.form-user__btn {
  margin-left: 1065px;
  margin-top: 5px;
}

.form-user label {
  display: block;
  font-family: inherit;
  font-size: 46px;
  font-weight: 300;
  font-variant: small-caps;
  color: var(--basic-label);
}

.form-user__personal input  {
  width: 100%;
  height: 30px;
  font-family: inherit;
  font-size: 23px;
  font-weight: 100;
  letter-spacing: -0.6px;
  color: #9c9c9c;
  border: none;
  border-bottom: 2px solid var(--back-basic);
  padding-top: 6px;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 6px;
}

p.form-user__field {
  margin-bottom: 65px;
}

.form-user__field label {
  margin-bottom: 16px;
}

.form-user__field textarea {
  width: 1332px;
  min-height: 155px;
  font-family: inherit;
  font-size: 23px;
  font-weight: 100;
  letter-spacing: -0.6px;
  color: #9c9c9c;
  border: none;
  border: 2px solid var(--back-basic);
  padding-top: 10px;
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 10px;
  resize: none;
}
