.catalog {
  margin-bottom: 40px;
}

.catalog__menu {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, max-content);
  column-gap: 10px;
  margin-bottom: 7px;
}

.catalog__menu li {
  width: 400px;
}

.catalog__wrap {
  display: grid;
  grid-template-columns: 400px 1fr;
  column-gap: 57px;
}

.catalog__wrap > ul {
  width: 400px;
  margin-top: 20px;
}
